import React, {Component} from 'react'
import Header from '../Header/Header'
import RandomPlanet from '../RandomPlanet/RandomPlanet'
import PeoplePage from '../PeoplePage/PeoplePage'
import PlanetPage from '../PlanetPage/PlanetPage'
import StarShipPage from '../StarShipPage/StarShipPage'
import ErrorBoundry from '../ErrorBoundry/ErrorBoundry'
import ItemDetails from '../ItemDetails/ItemDetails'
import SwapiService from '../../services/swapi-service'
import Field from '../Field/Field'
import {BrowserRouter as Router, Route} from 'react-router-dom'
import './App.css'

export default class App extends Component{

    swapiService = new SwapiService();

    render() {
        return (
            <div className = 'container'>
                <ErrorBoundry>
                    <Router>
                        <Header/>
                        <RandomPlanet/>
                        <Route path='/star-war-react' exact render={() => <span className='welcome'> Welcome to my application! Select a button.</span>} />
                        <Route path='/' exact render={() => <span className='welcome'> Welcome to my application! Select a button.</span>} />
                        <Route path='/people/:id?' component={PeoplePage} />
                        <Route path='/planet' component={PlanetPage} />
                        <Route path='/star_ship' exact component={StarShipPage} />
                        <Route path='/star_ship/:id' render = {({match}) => {
                            return  <ItemDetails selectedItemId = {match.params.id}
                                                 getData={this.swapiService.getStarShip} 
                                                 getImageUrl={this.swapiService.getImageUrlStarShip}
                                    > 
                                        <Field field='model' label='Model'/>
                                        <Field field='length' label='Length'/>
                                        <Field field='passengers' label='Passengers'/>
                                    </ItemDetails>          
                                }}
                            />
                    </Router>
                </ErrorBoundry>
            </div>
        )
    }
}


