import React, {Component} from 'react'
import ListItem from '../ListItem/ListItem'
import ItemDetails from '../ItemDetails/ItemDetails'
import Field from '../Field/Field'
import ErrorBoundry from '../ErrorBoundry/ErrorBoundry'
import Row from '../Row/Row'
import SwapiService from '../../services/swapi-service'

export default class PlanetPage extends Component{

    swapiService = new SwapiService();
    
    state = {
        selectedItemId: 5
    }

    selectedItem = (id) => {
        this.setState( {
            selectedItemId: id
        })   
    }

    render() {
         
        return (
                <Row left={<ListItem  selectedItem = {this.selectedItem} 
                                        getData={this.swapiService.getAllPlanets}
                            >
                            {(item => item.name)}
                            </ListItem>
                            }
                    right={
                        <ErrorBoundry>
                            <ItemDetails selectedItemId = {this.state.selectedItemId} 
                                        getData={this.swapiService.getPlanet} 
                                        getImageUrl={this.swapiService.getImageUrlPlanet}
                            >
                                <Field field='population' label='Population'/>
                                <Field field='diameter' label='Diametere'/>
                                <Field field='rotationPeriod' label='RotationPeriod'/>
                            </ItemDetails>
                            <span class='lets'>Let`s select item</span>
                        </ErrorBoundry>
                    }
                /> 
            )
        }
}




