import React from 'react';
import {Link} from 'react-router-dom'
import './Header.css';

const Header = () => {
  return (
    <div className="Header d-flex">
      <h3>
        <Link to="/">
          Star DB
        </Link>
      </h3>
      <ul className="d-flex">
        <li>
          <Link to="/people">People</Link>
        </li>
        <li>
          <Link to="/planet">Planets</Link>
        </li>
        <li>
          <Link to="/star_ship">Starships</Link>
        </li>
      </ul>
    </div>
  );
};

export default Header;