
export default class SwapiService {

    _apiUrl = 'https://swapi.co/api'

    getData = async(url) => {

        const resObj = await fetch(url);
        
        if (!resObj.ok) {
            throw new Error('Could not fetch 404') 
        }
        
        return await resObj.json(); 
    }


    /********PEOPLE******/
    getAllPeople = async() => {

        const res = await this.getData(`${this._apiUrl}/people/`)
        return res.results.map(this.transformPerson)
    }

    getPerson= async(id) => {
        const person = await this.getData(`${this._apiUrl}/people/${id}`);
        return this.transformPerson(person);
    }

    getImageUrlPerson = (item) => {

        return `https://starwars-visualguide.com/assets/img/characters/${item.id}.jpg`
    }
    
    /********PLANET******/
    getAllPlanets = async() => {

        const res = await this.getData(`${this._apiUrl}/planets/`);
        return  res.results.map(this.transformPlanet).slice(0,10)
    }

    getPlanet = async(id) => {

        const planet = await this.getData(`${this._apiUrl}/planets/${id}`)
        return this.transformPlanet(planet)
    }

    getImageUrlPlanet = (item) => {
       
        return `https://starwars-visualguide.com/assets/img/planets/${item.id}.jpg`
    }

    /********STAR SHIPS******/
    getAllStarShips = async() => {

        const res = await this.getData(`${this._apiUrl}/starships/`)
        return res.results.map(this.transformStarShip)
    }

    getStarShip = async(id) => {
   
        const starShip = await this.getData(`${this._apiUrl}/starships/${id}`)
        
        return this.transformStarShip(starShip)
    }

    getImageUrlStarShip = (item) => {

        return `https://starwars-visualguide.com/assets/img/starships/${item.id}.jpg`
    }

    /********transform************/

    extractId = (item) => {
     
        const idRegExp = /\/([0-9]*)\/$/;
        return item.url.match(idRegExp)[1];
      };

    transformPlanet = (planet) => {
        return {
            id: this.extractId(planet),
            name: planet.name,
            population: planet.population,
            rotationPeriod: planet.rotation_period,
            diameter: planet.diameter
        }
    }

    transformStarShip = (starShip) => {

        return {
            id: this.extractId(starShip),
            name: starShip.name,
            model: starShip.model,
            manufacturer: starShip.manufacturer,
            costInCredits: starShip.costInCredits,
            length: starShip.length,
            crew: starShip.crew,
            passengers: starShip.passengers,
            cargoCapacity: starShip.cargoCapacity
        }
    }

    transformPerson = (person) => {

        return {
            id: this.extractId(person),
            name: person.name,
            gender: person.gender,
            height: person.height,
            mass: person.mass
        }
    }
    
}


