import React, { Component } from 'react';
import SwapiService from '../../services/swapi-service'
import Spinner from '../Spinner/Spinner'
import ErrorIndicator from '../ErrorIndicator/ErrorIndicator'
import PropTypes from 'prop-types'
import './RandomPlanet.css';

export default class RandomPlanet extends Component {
  
  swapiService = new SwapiService();
  
  static defaultProps = {
    time: 1500
  }

  static propTypes = {
    time: PropTypes.number
  }

  state = {
    planet: {},
    loading: true,
    error: false
  }

  componentDidMount() {
    this.updatePlanet();
    this.interval = setInterval(this.updatePlanet, this.props.time)
  }

  componentWillUnmount() {
    clearInterval(this.interval)
  }

  updatePlanet = () => {

    const id = Math.floor(Math.random()*25 + 2); 
    this.swapiService.getPlanet(id)
      .then((planet) => {
        this.setState({
          planet,
          loading: false
        })
      })
      .catch(err => {
        this.setState({
          error: true,
          loading: false
        })
      })
  }


  render() {

    const hasData = !(this.state.loading || this.state.error)
    const spinner = this.state.loading ? <Spinner /> : null
    const content = hasData ? <PlanetView planet={this.state.planet}/> : null;
    const errorMessage = this.state.error ? <ErrorIndicator /> : null

    return (
      
      <div className="RandomPlanet jumbotron rounded">
        {errorMessage}
        {spinner}
        {content}
      </div>
    );
  }
}

const PlanetView = (props) => {

  return (
    <React.Fragment>
      <img className="planet-image" alt='planet' src={`https://starwars-visualguide.com/assets/img/planets/${props.planet.id}.jpg`} />
        <div>
          <h4>{props.planet.name}</h4>
          <ul className="list-group list-group-flush">
            <li className="list-group-item">
              <span className="term">Population</span>
              <span>{props.planet.population}</span>
            </li>
            <li className="list-group-item">
              <span className="term">Rotation Period</span>
              <span>{props.planet.rotationPeriod}</span>
            </li>
            <li className="list-group-item">
              <span className="term">Diameter</span>
              <span>{props.planet.diameter}</span>
            </li>
          </ul>
        </div>
    </React.Fragment>
  )
}