import React, { Component } from 'react';
import SwapiService from '../../services/swapi-service'
import Spinner from '../Spinner/Spinner'
import './ItemDetails.css';


export default class ItemDetails extends Component {

  static defaultProps = {
    selectedItemId: 5
  }
  swapiService = new SwapiService();

  state = {
    item: null,
    image: null,
    loading: true
  }

  updatePerson = () => {
    
    if(!this.props.selectedItemId) return
  
    this.props.getData(this.props.selectedItemId)
      .then((item) => this.setState({item, image: this.props.getImageUrl(item), loading: false})
    )
  }

  componentDidMount() {
   
    this.updatePerson()
  }

  componentDidUpdate(prevProps) {

    if ( this.props.selectedItemId !== prevProps.selectedItemId){
      this.setState({loading: true});
      this.updatePerson()
    }
  }

  render() {
    
    if(this.state.loading) return (
      <div className="PersonDetails card">
        <Spinner/>
      </div>
    )
    if(!this.state.item) return <span>Selected a person from a list</span>

    return (
      
      <div className="PersonDetails card">
        <img className="person-image" src={this.state.image} alt='person' />
        <div className="card-body">
          <h4>{this.state.item.name}</h4>
          <ul className="list-group list-group-flush">
            {
              React.Children.map(this.props.children, (child) => {
              return React.cloneElement(child, {item: this.state.item})
              })
            }
          </ul>
        </div>
      </div>
    )
  }
}