import React from 'react'
import ListItem from '../ListItem/ListItem'
import {withRouter} from 'react-router-dom'
import SwapiService from '../../services/swapi-service'

const StarShipPage = (props) => {
    
    const swapiService = new SwapiService();

    const selectedItem = (id) => {
        props.history.push(`/star_ship/${id}`)  
    }

    return (
        <div>
            <ListItem selectedItem = {selectedItem} 
                    getData={swapiService.getAllStarShips}
            > 
                    {(item) => (<div className = 'd-flex justify-content-between'><span >{item.name}</span><span className = 'd-flex'>{item.model}</span></div>)}
            </ListItem> 
            <span class='lets'>Let`s select item</span>
        </div>
    )
}

export default withRouter(StarShipPage)



