import React from 'react'
import ListItem from '../ListItem/ListItem'
import ItemDetails from '../ItemDetails/ItemDetails'
import Field from '../Field/Field'
import ErrorBoundry from '../ErrorBoundry/ErrorBoundry'
import Row from '../Row/Row'
import SwapiService from '../../services/swapi-service'
import {withRouter} from 'react-router-dom'
import './PeoplePage.css'

const PeoplePage = (props) => {

    const swapiService = new SwapiService();
    
    const selectedItem = (id) => {
        props.history.push(`/people/${id}`)

    }

    console.log();
    
        return (
                <Row 
                    left={<ListItem selectedItem = {selectedItem} 
                                    getData={swapiService.getAllPeople}
                        >
                                {(item) => (<div className = 'd-flex justify-content-between'><span >{item.name}</span><span className = 'd-flex'>{item.gender}</span></div>)}
                        </ListItem>   
                        } 
                    right={
                        <ErrorBoundry>
                            <ItemDetails selectedItemId = {props.match.params.id} 
                                         getData={swapiService.getPerson} 
                                         getImageUrl={swapiService.getImageUrlPerson}
                            >
                                <Field field='gender' label='Gender'/>
                                <Field field='height' label='Height'/>
                                <Field field='mass' label='Mass'/>
                            </ItemDetails>
                            <span class='lets'>Let`s select item</span>
                        </ErrorBoundry>
                    }
                />
        )
    
}

export default withRouter(PeoplePage)



