import React, { Component } from 'react';
import Spinner from '../Spinner/Spinner'
import ErrorIndicator from '../ErrorIndicator/ErrorIndicator'
import './ListItem.css';

export default class ListItem extends Component {
 
  state = {
    itemList: null,
    loading: true,
    error: false
  }
 
  componentDidMount() {
    this.props.getData()
      .then(itemList => {
        this.setState({itemList, loading: false})
      })
      .catch(err => {
        this.setState({
          error: true,
          loading: false
        })
      })
  }

  render() {

    const hasData = !(this.state.loading || this.state.error)
    const spinner = this.state.loading ? <Spinner /> : null
    const content = hasData ? <ListView itemList={this.state.itemList} selectedItem={this.props.selectedItem} child={this.props.children}>  </ListView> : null;
    const errorMessage = this.state.error ? <ErrorIndicator /> : null

    return (

      <ul className="ListItem list-group">
        {errorMessage}
        {spinner}
        {content}
      </ul>

    );
  }
}

const ListView = (props) => {

  const itemList = props.itemList.map(item => {

    const label = props.child(item)
    return (
      <li key = {item.name} 
          className="list-group-item" 
          onClick = {() => props.selectedItem(item.id)}>
          {label}
      </li>
    )
  })

  return (
    <React.Fragment>
      {itemList}
    </React.Fragment>
  )
}
